import React, { useState, useEffect } from "react";
import { Table, Button, InputGroup, FormControl, Dropdown, Form, Modal } from "react-bootstrap";
import PaginationComponent from "./PaginationComponent"; // Your reusable pagination component
import styles from "../styles/product.module.css";

const ProductPage = () => {
  const [products, setProducts] = useState([]);
  const [filteredProducts, setFilteredProducts] = useState([]);
  const [searchQuery, setSearchQuery] = useState("");
  const [selectedCategory, setSelectedCategory] = useState("");
  const [showModal, setShowModal] = useState(false);
  const [newProduct, setNewProduct] = useState({});
  const [isEditing, setIsEditing] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage] = useState(5);

  const categories = ["Electronics", "Books", "Clothing", "Home Appliances"];

  useEffect(() => {
    // Fetch products from a mock API or static array
    const fetchedProducts = [
      { id: 1, name: "Laptop", price: "$1200", category: "Electronics", status: "Active", addedDate: "2024-12-15" },
      { id: 2, name: "Smartphone", price: "$800", category: "Electronics", status: "Active", addedDate: "2024-12-14" },
      { id: 3, name: "Novel", price: "$20", category: "Books", status: "Inactive", addedDate: "2024-12-12" },
      { id: 4, name: "T-Shirt", price: "$25", category: "Clothing", status: "Active", addedDate: "2024-12-10" },
      { id: 5, name: "Microwave", price: "$150", category: "Home Appliances", status: "Inactive", addedDate: "2024-12-08" },
    ];
    setProducts(fetchedProducts);
    setFilteredProducts(fetchedProducts);
  }, []);

  // Handle filtering by category
  const handleCategoryFilter = (category) => {
    setSelectedCategory(category);
    filterProducts(searchQuery, category);
  };

  // Handle search query
  const handleSearch = (event) => {
    const query = event.target.value;
    setSearchQuery(query);
    filterProducts(query, selectedCategory);
  };

  // Filter products based on search and category
  const filterProducts = (query, category) => {
    let filtered = products.filter(
      (product) =>
        product.name.toLowerCase().includes(query.toLowerCase()) ||
        product.category.toLowerCase().includes(query.toLowerCase())
    );
    if (category) {
      filtered = filtered.filter((product) => product.category === category);
    }
    setFilteredProducts(filtered);
    setCurrentPage(1);
  };

  // Handle modal form
  const handleShowModal = (product = null) => {
    setNewProduct(product || {});
    setIsEditing(!!product);
    setShowModal(true);
  };

  const handleCloseModal = () => {
    setShowModal(false);
    setNewProduct({});
  };

  // Handle form submission
  const handleFormSubmit = () => {
    if (isEditing) {
      const updatedProducts = products.map((p) =>
        p.id === newProduct.id ? newProduct : p
      );
      setProducts(updatedProducts);
      setFilteredProducts(updatedProducts);
    } else {
      const newId = products.length ? products[products.length - 1].id + 1 : 1;
      const productWithId = { ...newProduct, id: newId, addedDate: new Date().toISOString().split("T")[0] };
      const updatedProducts = [...products, productWithId];
      setProducts(updatedProducts);
      setFilteredProducts(updatedProducts);
    }
    handleCloseModal();
  };

  // Handle pagination
  const paginatedProducts = filteredProducts.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  return (
    <div className="container">
      <h1 className={styles.pageTitle}>Product Management</h1>

      {/* Filters */}
      <div className="row mb-4">
        <div className="col-md-6">
          <InputGroup>
            <FormControl
              placeholder="Search by product name or category"
              value={searchQuery}
              onChange={handleSearch}
            />
          </InputGroup>
        </div>
        <div className="col-md-6 d-flex justify-content-end">
          <Dropdown onSelect={handleCategoryFilter}>
            <Dropdown.Toggle variant="outline-secondary">
              {selectedCategory || "Filter by Category"}
            </Dropdown.Toggle>
            <Dropdown.Menu>
              <Dropdown.Item eventKey="">All Categories</Dropdown.Item>
              {categories.map((category) => (
                <Dropdown.Item key={category} eventKey={category}>
                  {category}
                </Dropdown.Item>
              ))}
            </Dropdown.Menu>
          </Dropdown>
          <Button className="ms-3" onClick={() => handleShowModal()}>
            Add Product
          </Button>
        </div>
      </div>

      {/* Product Table */}
      <Table striped bordered hover>
        <thead className={styles.tableHeader}>
          <tr>
            <th>#</th>
            <th>Product Name</th>
            <th>Price</th>
            <th>Category</th>
            <th>Status</th>
            <th>Date Added</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {paginatedProducts.map((product, index) => (
            <tr key={product.id}>
              <td>{(currentPage - 1) * itemsPerPage + index + 1}</td>
              <td>{product.name}</td>
              <td>{product.price}</td>
              <td>{product.category}</td>
              <td>
                <span
                  className={`${styles.statusBadge} ${
                    product.status === "Active" ? styles.active : styles.inactive
                  }`}
                >
                  {product.status}
                </span>
              </td>
              <td>{product.addedDate}</td>
              <td>
                <Button variant="info" size="sm" onClick={() => handleShowModal(product)}>
                  Edit
                </Button>
                <Button variant="danger" size="sm" className="ms-2">
                  Delete
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>

      {/* Pagination */}
      <PaginationComponent
        totalItems={filteredProducts.length}
        itemsPerPage={itemsPerPage}
        currentPage={currentPage}
        onPageChange={(page) => setCurrentPage(page)}
      />

      {/* Modal */}
      <Modal show={showModal} onHide={handleCloseModal}>
        <Modal.Header closeButton>
          <Modal.Title>{isEditing ? "Edit Product" : "Add Product"}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form className={styles.modalForm}>
            <Form.Group controlId="formProductName">
              <Form.Label>Product Name</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter product name"
                value={newProduct.name || ""}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, name: e.target.value })
                }
              />
            </Form.Group>
            <Form.Group controlId="formProductPrice">
              <Form.Label>Price</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter product price"
                value={newProduct.price || ""}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, price: e.target.value })
                }
              />
            </Form.Group>
            <Form.Group controlId="formProductCategory">
              <Form.Label>Category</Form.Label>
              <Form.Select
                value={newProduct.category || ""}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, category: e.target.value })
                }
              >
                <option value="">Select Category</option>
                {categories.map((category) => (
                  <option key={category} value={category}>
                    {category}
                  </option>
                ))}
              </Form.Select>
            </Form.Group>
            <Form.Group controlId="formProductStatus">
              <Form.Label>Status</Form.Label>
              <Form.Select
                value={newProduct.status || ""}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, status: e.target.value })
                }
              >
                <option value="Active">Active</option>
                <option value="Inactive">Inactive</option>
              </Form.Select>
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button className={styles.secondaryButton} onClick={handleCloseModal}>
            Close
          </Button>
          <Button className={styles.primaryButton} onClick={handleFormSubmit}>
            {isEditing ? "Update Product" : "Add Product"}
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default ProductPage;
